import React from 'react';
import GoodsList from './GoodsList';
import './App.css';

const goodsFromServer = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

class App extends React.Component {
  state = {
    goodsList: goodsFromServer,
    list: '',
  }

  render() {
    return (
      <div className="App">
        <h1>Goods</h1>
        {this.state.list}
        {this.state.list === ''
          &&
            <button
              type="button"
              onClick={() => {
                this.setState({ list: <GoodsList goodsList={this.state.goodsList} /> })
              }}
            >
              Start
            </button>
        }
      </div>
    )
  }
};

export default App;
